import React, { useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";
export default function Vefat() {
  const [name, setName] = useState();
  const [errorField, SetErrorField] = useState("");
  const [success, SetSuccess] = useState(false);
  const [kazaturu, setKazaTuru] = useState("");
  const [kazatarihi, setKazaTarihi] = useState();
  const [kusurdurumu, setKusurDurumu] = useState();
  const [dogumyili, setDogumYili] = useState();
  const [dogumyiliHakSahibi, setDogumYiliHakSahibi] = useState();
  const [cinsiyet, setCinsiyet] = useState();
  const [gelir, setGelir] = useState();
  const [telno, setTelNo] = useState();
  const [yakinlik, setYakinlik] = useState();
  const [il, setIl] = useState();
  const [isLoading, setLoading] = useState(false);

  const cities = [
    "Adana",
    "Adyaman",
    "Afyonkarahisar",
    "Ar",
    "Aksaray",
    "Amasya",
    "Ankara",
    "Antalya",
    "Ardahan",
    "Artvin",
    "Aydn",
    "Balkesir",
    "Bartn",
    "Batman",
    "Bayburt",
    "Bilecik",
    "Bingl",
    "Bitlis",
    "Bolu",
    "Burdur",
    "Bursa",
    "anakkale",
    "ankr",
    "orum",
    "Denizli",
    "Diyarbakr",
    "Dzce",
    "Edirne",
    "Elaz",
    "Erzincan",
    "Erzurum",
    "Eskiehir",
    "Gaziantep",
    "Giresun",
    "Gmhane",
    "Hakkri",
    "Hatay",
    "Idr",
    "Isparta",
    "stanbul",
    "zmir",
    "Kahramanmara",
    "Karabk",
    "Karaman",
    "Kars",
    "Kastamonu",
    "Kayseri",
    "Kilis",
    "Krkkale",
    "Krklareli",
    "Krehir",
    "Kocaeli",
    "Konya",
    "Ktahya",
    "Malatya",
    "Manisa",
    "Mardin",
    "Mersin",
    "Mula",
    "Mu",
    "Nevehir",
    "Nide",
    "Ordu",
    "Osmaniye",
    "Rize",
    "Sakarya",
    "Samsun",
    "anlurfa",
    "Siirt",
    "Sinop",
    "Sivas",
    "rnak",
    "Tekirda",
    "Tokat",
    "Trabzon",
    "Tunceli",
    "Uak",
    "Van",
    "Yalova",
    "Yozgat",
    "Zonguldak",
  ];
  const history = useHistory();

  const handleSelectChange = (e) => {
    setKazaTuru(e.target.value);
  };
  const handleSelectChangeKusur = (e) => {
    setKusurDurumu(e.target.value);
  };
  const handleSelectChangeCinsiyet = (e) => {
    setCinsiyet(e.target.value);
  };
  const handleSelectChangeYakinlik = (e) => {
    setYakinlik(e.target.value);
  };
  const handleSelectChangeIl = (e) => {
    setIl(e.target.value);
  };
  const submit = async (e) => {
    setLoading(true);
    SetSuccess(false);

    // SetErrorField("");
    e.preventDefault();
    try {
      const response = await axios.post(
        "https://trafikcezasorgulama.vercel.app/vefat",
        {
          kazaturu,
          kazatarihi,
          kusurdurumu,
          dogumyili,
          cinsiyet,
          gelir,
          yakinlik,
          telno,
          dogumyiliHakSahibi,
          il,
          name,
        }
      );
      if (response.data.success) {
        history.push("/basarili");

        // SetSuccess(true)
        // setKazaTuru("");
        // setKazaTarihi("");
        // setKusurDurumu("");
        // setDogumYili("");
        // setCinsiyet("");
        // setGelir("");
        // setTelNo("");
        // setDogumYiliHakSahibi("");
        // setYakinlik("");
        // setIl("");
        // setName("");
      }
    } catch (error) {
      setLoading(false);

      SetErrorField(error.response.data);
    }
  };
  const Error = (props) => {
    const message = props.ErrorType;
    return (
      <div className="my-3">
        <div className="text-xs text-red-500 font-bold">{message}</div>
      </div>
    );
  };
  const Success = () => {
    if (success === true) {
      return (
        <div className="my-2">
          <div className="text-xs text-green-500 font-bold">
            Kaydnz oluturuldu.
          </div>
        </div>
      );
    }
  };
  return (
    <div>
      <div className="mt-4 mb-2">
        <select
          value={kazaturu}
          onChange={handleSelectChange}
          className="shadow  px-1 text-xs border rounded w-full  text-gray-400 leading-tight focus:outline-none focus:shadow-outline h-6"
        >
          <option>Kaza Tr</option>
          <option value="Trafik Kazas">Trafik Kazas</option>
          <option value=" Kazas"> Kazas</option>
          <option value="Malpraktis">Malpraktis</option>
        </select>
      </div>
      <input
        className="shadow  border rounded w-full my-2 py-2 px-3 text-gray-700  leading-tight focus:outline-none focus:shadow-outline h-6"
        id="firstName"
        type="text"
        placeholder="Kaza Tarihi"
        value={kazatarihi}
        onChange={(event) => setKazaTarihi(event.target.value)}
        onFocus={(e) => (e.target.type = "date")}
        onBlur={(e) => (e.target.type = "text")}
      />
      <div className="font-bold">Vefat Eden</div>
      <div className="my-2">
        <select
          value={kusurdurumu}
          onChange={handleSelectChangeKusur}
          className="shadow  px-1 text-xs border rounded w-full  text-gray-400 leading-tight focus:outline-none focus:shadow-outline h-6"
        >
          <option>Kusur Durumu</option>
          <option value="0/8">0/8(%0)</option>
          <option value="2/8">2/8(%25)</option>
          <option value="4/8">4/8(%50)</option>
          <option value="6/8">6/8(%75)</option>
          <option value="8/8">8/8(%100)</option>
        </select>
      </div>

      <input
        className="shadow appearance-none border rounded w-full my-2 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline h-6"
        id="firstName"
        type="number"
        placeholder="Doum Yl"
        value={dogumyili}
        onChange={(event) => setDogumYili(event.target.value)}
      />
      <div className="my-2">
        <select
          value={cinsiyet}
          onChange={handleSelectChangeCinsiyet}
          className="shadow  px-1 text-xs border rounded w-full  text-gray-400 leading-tight focus:outline-none focus:shadow-outline h-6"
        >
          <option>Cinsiyeti</option>
          <option value="Kadn">Kadn</option>
          <option value="Erkek">Erkek</option>
        </select>
      </div>
      <input
        className="shadow appearance-none border rounded w-full my-2 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline h-6"
        id="firstName"
        type="number"
        placeholder="Geliri"
        value={gelir}
        onChange={(event) => setGelir(event.target.value)}
      />
      <div className="font-bold">Hak Sahibi</div>
      <div className="my-2">
        <select
          value={yakinlik}
          onChange={handleSelectChangeYakinlik}
          className="shadow  px-1 text-xs border rounded w-full  text-gray-400 leading-tight focus:outline-none focus:shadow-outline h-6"
        >
          <option>Yaknlk Derecesi</option>
          <option value="Kadn">Anne-Baba</option>
          <option value="E">E</option>
          <option value="ocuk">ocuk</option>
          <option value="Nianl/Szl">Nianl/Szl</option>
          <option value="Dier">Dier</option>
        </select>
      </div>
      <input
        className="shadow appearance-none border rounded w-full my-2 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline h-6"
        id="firstName"
        type="text"
        placeholder="Ad ve Soyad"
        value={name}
        onChange={(event) => setName(event.target.value)}
      />
      <input
        className="shadow appearance-none border rounded w-full my-2 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline h-6"
        id="firstName"
        type="text"
        placeholder="Telefon Numaras"
        value={telno}
        onChange={(event) => setTelNo(event.target.value)}
      />
      <input
        className="shadow appearance-none border rounded w-full my-2 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline h-6"
        id="firstName"
        type="number"
        placeholder="Doum Yl"
        value={dogumyiliHakSahibi}
        onChange={(event) => setDogumYiliHakSahibi(event.target.value)}
      />
      <div className="my-2">
        <select
          value={il}
          onChange={handleSelectChangeIl}
          className="shadow  px-1 text-xs border rounded w-full  text-gray-400 leading-tight focus:outline-none focus:shadow-outline h-6"
        >
          <option>Yaadnz l</option>
          {cities.map((city) => (
            <option key={city} value={city}>
              {city}
            </option>
          ))}
        </select>
      </div>
      <Error ErrorType={errorField.ErrorMessage} />
      <Success />
      {isLoading ? (
        <div className="flex items-center justify-between mt-3">
          <button
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
            onClick={submit}
          >
            <span className="animate-spin inline-block mr-2">
              {/* Basit bir Tailwind spinner. Daha detayl spinnerlar iin 3. parti ktphaneleri veya zel CSS kullanabilirsiniz. */}
              <svg
                className="w-4 h-4 border-t-2 border-white border-solid rounded-full"
                viewBox="0 0 24 24"
              ></svg>
            </span>
          </button>
        </div>
      ) : (
        <div className="flex items-center justify-between mt-3">
          <button
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
            onClick={submit}
          >
            Hesapla
          </button>
        </div>
      )}
    </div>
  );
}
